import Vue from 'vue';
//import Vuetify from 'vuetify';
 export default {
 	name: 'Postsys',
 	props: ["posts", "board"],
 	methods: {


  idlists:  function(post_id) {
      for(var i = 0; i < this.board.length; i++) {
     if(this.board[i] == post_id)
        return post_id;
      }
   return 0;
   },

  postFilter: function(posts) {

       return this.posts.filter(post => post.id==this.idlists(post.id));
     }
  },

};
 